/**
 * eventBus
 */
export class EventDispatcher {
  private eventList = {}; // 

  constructor() {}

  /**
   * 
   * @param type 
   * @param args 
   */
  public dispatchEvent(type: string, ...args: any[]): void {
    let arr: Array<any> = this.eventList[type];
    if (arr) {
      arr.forEach(element => {
        element[0].apply(element[1], args);
        // listener.call(thisObject, args);
      });
    }
  }

  /**
   * 
   * @param type       
   * @param listener   
   * @param thisObject 
   */
  public addEventListener(
    type: string,
    listener: Function,
    thisObject: any
  ): void {
    let arr: Array<any> = this.eventList[type];
    if (!arr) {
      arr = [];
      this.eventList[type] = arr;
    }
    // else
    // {
    //     arr.forEach(element =>
    //     {
    //         if(element[0] == listener && element[1] == thisObject)
    //         {
    //             return;
    //         }
    //     });
    // }
    arr.push([listener, thisObject]);
  }

  /**
   * 
   * @param type       
   * @param listener   
   * @param thisObject 
   */
  public removeEventListener(
    type: string,
    listener: Function,
    thisObject: any
  ): void {
    let arr: Array<any> = this.eventList[type];
    if (arr) {
      let len = arr.length;
      for (let i = len - 1; i >= 0; i--) {
        if (arr[i][0] === listener && arr[i][1] === thisObject) {
          arr.splice(i, 1);
        }
      }
    }
    if (arr && arr.length === 0) {
      this.eventList[type] = null;
      delete this.eventList[type];
    }
  }
}
